@import url(http://fonts.cdnfonts.com/css/bebas-kai);
body {
  margin: 0;
  width: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
  only screen and (-o-min-device-pixel-ratio: 3/2),
  only screen and (min--moz-device-pixel-ratio: 1.5),
  only screen and (min-device-pixel-ratio: 1.5) {
  html,
  body {
    width: 100%;
    overflow-x: hidden;
  }
}

@media only screen and (max-width: 1280px) {
  html {
    font-size: 14px !important;
  }
}

.wrapper {
  background: url(/assix-serigrafia/static/media/bg.3b65101d.svg) no-repeat;
  background-size: cover;
  background-color: #e9e9e9;
  width: 100%;
  height: 100vh;
}
.hero {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  color: #232323;
  height: 100vh;
}

.hero--wrapper {
  grid-column: 8/12;
  align-self: center;
}

.hero--text h3 {
  text-align: left;
  margin-bottom: 0.5em;
}

.hero--button {
  box-shadow: 0px 0px 10px -1px rgba(255, 0, 0, 1) !important;
  -webkit-box-shadow: 0px 0px 16px 1px rgba(255, 0, 0, 1);
  -moz-box-shadow: 0px 0px 16px 1px rgba(255, 0, 0, 1);
}

@media only screen and (max-width: 1280px) {
  .wrapper {
    background: url(/assix-serigrafia/static/media/mdBg.5fd12ab8.svg) no-repeat;
    background-size: cover;
    background-color: #e9e9e9;
    width: 100%;
    height: 100vh;
  }
  .hero {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    color: #232323;
    height: 100vh;
  }

  .hero--wrapper {
    grid-column: 8/12;
    align-self: center;
  }

  .hero--text h3 {
    text-align: left;
    margin-bottom: 0.5em;
  }
}

@media only screen and (orientation: portrait) {
  .wrapper {
    background: url(/assix-serigrafia/static/media/responsiveBg.97a07592.svg) no-repeat;
    background-size: cover;
    height: 800px;
  }

  .hero {
    color: white !important;
    height: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
  }

  .hero--wrapper {
    text-align: center !important;
  }

  .hero--text h3 {
    color: white;
    text-align: center;
    margin-bottom: 0.5em;
  }
}

h2 {
  color: white;
  text-align: center;
}

.canal--wrapper {
  background: url(/assix-serigrafia/static/media/canalBg.bb554ced.svg) no-repeat;
  background-size: cover;
}

.card {
  width: 300px;
  height: 300px;
}

.videos--wrapper {
  background: url(/assix-serigrafia/static/media/videosBg.44ee1775.svg) no-repeat;
  background-size: cover;
}

h3,
h4 {
  color: #e9e9e9;
  text-align: center;
}

.comunidad--wrapper {
  background: url(/assix-serigrafia/static/media/comunidadBg.e5766473.svg) no-repeat;
  background-size: cover;
}

